import BigNumber from "bignumber.js";
import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import useRefresh from "hooks/useRefresh";
import { fetchFarmsPublicDataAsync, fetchPoolsPublicDataAsync, fetchPoolsUserDataAsync } from "./actions";
import { State, Farm, Pool } from "./types";

const coinToTickerSymbol = {
  "binancecoin"      : "BNB",
  "binance-usd"      : "BUSD",
  "true-usd"         : "TUSD",
  "tether"           : "USDT",
  "usd-coin"         : "USDC",
  "binance-bitcoin"  : "BTCB",
  "dai"              : "DAI",
  "ethereum"         : "ETH",
  "litecoin"         : "LTC",
  "pancakeswap-token": "CAKE",
  "chainlink"        : "LINK",
}

const ZERO = new BigNumber(0)

export const useFetchPublicData = () => {
  const dispatch = useDispatch()
  const { slowRefresh } = useRefresh()

  useEffect(() => {
    dispatch(fetchFarmsPublicDataAsync())
    dispatch(fetchPoolsPublicDataAsync())
  }, [dispatch, slowRefresh])
}

export const useFarms = (): Farm[] => {
  const farms = useSelector((state: State) => state.farms.data)
  return farms
}

export const useFarmFromPid = (pid): Farm => {
  const farm = useSelector((state: State) => state.farms.data.find((f) => f.pid === pid))
  return farm
}

export const useFarmFromSymbol = (lpSymbol: string): Farm => {
  const farm = useSelector((state: State) => state.farms.data.find((f) => f.lpSymbol === lpSymbol))
  return farm
}

export const useFarmUser = (pid) => {
  const farm = useFarmFromPid(pid)

  return {
    allowance: farm.userData ? new BigNumber(farm.userData.allowance) : new BigNumber(0),
    tokenBalance: farm.userData ? new BigNumber(farm.userData.tokenBalance) : new BigNumber(0),
    stakedBalance: farm.userData ? new BigNumber(farm.userData.stakedBalance) : new BigNumber(0),
    earnings: farm.userData ? new BigNumber(farm.userData.earnings) : new BigNumber(0),
  }
}

export const usePools = (account): Pool[] => {
  const { fastRefresh } = useRefresh()
  const dispatch = useDispatch()
  useEffect(() => {
    if (account) {
      dispatch(fetchPoolsUserDataAsync(account))
    }
  }, [account, dispatch, fastRefresh])

  const pools = useSelector((state: State) => state.pools.data)
  return pools
}

export const usePoolFromPid = (sousId): Pool => {
  const pool = useSelector((state: State) => state.pools.data.find((p) => p.sousId === sousId))
  return pool
}

export const usePriceBnbBusd = (): BigNumber => {
  const farm = useFarmFromPid(2); // BNB-BUSD on PancakeSwap
  return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO;
}

export const usePriceCakeBusd = (): BigNumber => {
  const bnbBusd = usePriceBnbBusd();
  const farm = useFarmFromPid(1); // UPWARD-BNB LP

  if (bnbBusd.toNumber()) {
    if (farm.tokenPriceVsQuote.toString() !== "0") {
      return new BigNumber(farm.tokenPriceVsQuote).dividedBy(bnbBusd.toNumber());
    }

    return new BigNumber(0.0001);
  }

  return ZERO;
}

export const usePriceBitcoin = (): BigNumber => {
  const farm = useFarmFromPid(11); // BTCB-BUSD LP
  return farm.tokenPriceVsQuote ? new BigNumber(1).div(farm.tokenPriceVsQuote) : ZERO;
}

const useGetPriceData: any = () => {
  // https://api.coingecko.com/api/v3/coins/list?include_platform=true

  const coins = [];

  for (const coin in coinToTickerSymbol) {
    coins.push(coin);
  }

  const api = `https://api.coingecko.com/api/v3/simple/price?ids=${coins.join(",")}&vs_currencies=usd`;
  const [data, setData] = useState({});

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(api)
        const res = await response.json()
        const tokenPrices = {};

        for (const coinString in res) {
          const price = res[coinString].usd;
          const tickerSymbol = coinToTickerSymbol[coinString];
          tokenPrices[tickerSymbol] = new BigNumber(price);

          if (tickerSymbol === "BNB") {
            tokenPrices["WBNB"] = new BigNumber(price);
          }
        }

        setData(tokenPrices);
      } catch (error) {
        console.error("Unable to fetch price data:", error)
      }
    }

    fetchData()
  }, [api, setData]);

  return data;
}

export const useTotalValue = (): BigNumber => {
  const farms = useFarms();
  const pools = usePools("0x0000000000000000000000000000000000000000"); // We don't need specific user data here
  const bnbPrice = usePriceBnbBusd();
  const cakePrice = usePriceCakeBusd();
  const btcPrice = usePriceBitcoin();
  let totalValue = new BigNumber(0);
  const priceData = useGetPriceData();

  priceData["UPWARD"] = cakePrice;

  for (let i = 0; i < pools.length; i++) {
    const {
      stakingTokenName,
      tokenDecimals,
      totalStaked
    } = pools[i];

    if (!totalStaked || (new BigNumber(totalStaked)).toNumber() === 0) {
      continue;
    }

    const power = (new BigNumber(10)).pow(tokenDecimals);
    const wholeTokensStaked = new BigNumber(totalStaked).div(power);
    const value = wholeTokensStaked.multipliedBy(priceData[stakingTokenName]);

    totalValue = totalValue.plus(value);
  }

  for (let i = 0; i < farms.length; i++) {
    const farm = farms[i];

    if (farm.lpTotalInQuoteToken) {
      let value = new BigNumber(0);

      if (farm.tokenSymbol === "BUSD" && farm.quoteTokenSymbol === "BNB") {
        value = bnbPrice.times(farm.lpTotalInQuoteToken);
      } else if (farm.quoteTokenSymbol === "BNB") {
        if ((new BigNumber(farm.lpTotalInQuoteToken)).toNumber() === 0) {
          continue;
        }

        value = bnbPrice.times(farm.lpTotalInQuoteToken);

      } else if (farm.quoteTokenSymbol === "BUSD" ||
                 farm.quoteTokenSymbol === "USDT" ||
                 farm.quoteTokenSymbol === "USDC"
      ) {
        value = farm.lpTotalInQuoteToken;
      } else if (farm.quoteTokenSymbol === "ETH") {
        // Every single farm LP pair either contains BNB or a stablecoin, the only exception to this is BTCB-ETH
        value = btcPrice.times(farm.lpTotalInQuoteToken);
      }

      totalValue = totalValue.plus(value);
    }
  }

  return totalValue;
}
